.topbar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
}

/* ìƒë‹¨ ì˜ì—­ (ë¡œê³  + ì¸ì¦ ë²„íŠ¼) */
.topbar-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    width: 100%;
    box-sizing: border-box;
}

.topbar-auth-controls {
    display: flex;
    width: 100%;
    align-items: center;
}

.auth-controls-btn {
    white-space: nowrap;
    margin-left: auto;
}

.topbar-logo {
    margin: 0;
    color: #333;
    text-decoration: none;
}

.topbar-logo:hover {
    color: #333;
    text-decoration: none;
}

