.room-tabs-container {
    width: 100%;
    display: flex;
    justify-content: center;
    border-top: 1px solid #eee;
}

/* ê°œë³„ ë£¸ íƒ­ ë²„íŠ¼ */
.room-tab {
    padding: 10px 15px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 15px;
    color: #555;
    border-bottom: 3px solid transparent;
}

.room-tab:hover {
    background-color: #f8f9fa;
}

/* í™œì„±í™”ëœ íƒ­ ìŠ¤íƒ€ì¼ */
.room-tab.active {
    color: #007bff;
    font-weight: bold;
    border-bottom: 3px solid #007bff;
}

/* ì•ˆ ì½ì€ ë©”ì‹œì§€ê°€ ìˆëŠ” íƒ­ ìŠ¤íƒ€ì¼ */
.room-tab.unread {
    position: relative;
    font-weight: bold;
}

.room-tab.unread::after {
    content: '';
    position: absolute;
    top: 10px;
    right: 8px;
    width: 8px;
    height: 8px;
    background-color: #007bff;
    border-radius: 50%;
}

.danger-button { background-color: #dc3545; color: white; border-color: #dc3545; }

