.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    overflow-y: auto;
    padding: 2rem;
    box-sizing: border-box;
}

@media (max-width: 768px) {
    .modal-overlay {
        display: block;
        padding: 1rem;
    }

    .modal-content {
        margin: 3rem auto 0 auto;
    }
}

.modal-content {
    position: relative;
    background-color: white;
    padding: 30px 40px;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    width: 90%;
    max-width: 450px;
    box-sizing: border-box;
}

.modal-close-btn {
    position: absolute;
    top: 10px;
    right: 15px;
    background: none;
    border: none;
    font-size: 28px;
    color: #aaa;
    cursor: pointer;
}

.modal-close-btn:hover {
    color: #333;
}

.modal-content h2 {
    text-align: center;
    margin-top: 0;
    margin-bottom: 25px;
}

.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: 500;
    color: #555;
}

.form-group input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.modal-submit-btn {
    width: 100%;
    padding: 12px;
    border: none;
    border-radius: 4px;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
}

.modal-submit-btn:hover {
    background-color: #0056b3;
}

/* --- í”„ë¡œí•„ ìˆ˜ì • ëª¨ë‹¬ ì¶”ê°€ ìŠ¤íƒ€ì¼ --- */
.profile-image-area {
    text-align: center;
    margin-bottom: 20px;
}

.profile-image-preview {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid #eee;
    margin-bottom: 10px;
}

.profile-image-label {
    cursor: pointer;
    color: #007bff;
    font-weight: bold;
}

.profile-image-label:hover {
    text-decoration: underline;
}

.form-group textarea {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
    min-height: 80px;
}

