.lobby-container {
    width: 100%;
    max-width: 1200px; /* ë” ë„“ì€ ìµœëŒ€ ë„ˆë¹„ */
    margin: 0 auto;
    padding: 20px;
    box-sizing: border-box;
}

.lobby-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.create-room-btn {
    padding: 10px 15px;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 5px;
    cursor: pointer;
}

.room-list {
    margin-bottom: 30px;
}

/* âœ… 1. ul íƒœê·¸ë¥¼ Flexbox ì»¨í…Œì´ë„ˆë¡œ ë§Œë“­ë‹ˆë‹¤. */
.room-list ul {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 15px; /* ì¹´ë“œ ì‚¬ì´ì˜ ê°„ê²© */
}

/* li íƒœê·¸ë¥¼ 'ì¹´ë“œ' í˜•íƒœë¡œ ë””ìì¸ (ë°ìŠ¤í¬í†± ê¸°ë³¸) */
.room-list-item {
    width: 220px;
    height: 110px;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    position: relative;
}

.room-list-item:hover {
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
    transform: translateY(-3px);
}

.room-list-item.my-room {
    border-left: 4px solid #007bff;
}

.room-info {
    padding-right: 40px;
}

.room-name {
    font-size: 1.2em;
    font-weight: bold;
    margin-bottom: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.lock-icon {
    margin-right: 8px;
}

.room-owner {
    font-size: 0.85em;
    color: #666;
}
.room-name,
.room-owner {
    display: block;
}

/* ì¹´ë“œ ìš°ì¸¡ ìƒë‹¨ë¶€ (ì¸ì›ìˆ˜) */
.room-meta {
    position: absolute;
    top: 12px;
    right: 12px;
}

.room-count {
    font-size: 1em;
    font-weight: bold;
}

.room-count.online {
    color: #28a745;
    font-weight: bold;
    display: flex;
    align-items: center;
    gap: 5px;
}

.no-rooms {
    color: #888;
    padding: 20px;
    text-align: center;
    width: 100%;
}

